@mixin base() {
    display: flex;
    flex-direction: column;
    list-style: none;
    margin: 0;
    overflow: hidden; // fixes IE bug that adds blank space on :hover
    padding: 0;

    @media screen and (min-width: breakpoint(medium)) {
        flex-direction: row;
    }
}

&__list {
    @include base;
}
