//add to theme-specific.scss
$theme-base-4: $color-neutral-dark;
$theme-text-accent: $color-steel-blue;

//add to new feature component folder
$component-feature-background-color: $theme-base-2;
$component-feature-heading-color: $theme-text-accent;
$component-feature-box-shadow-color: rgba($theme-primary-4, 0.15);

$component-feature-content-max-width: 600px;
$component-feature-content-min-height: 300px;
$component-feature-content-after-width-percent: 89%;
$component-feature-content-background-color: $theme-white;

$component-feature-shadow-border-color: $theme-white;
$component-feature-shadow-background-color: radial-gradient(circle, rgba($theme-base, 0.5) 0%, rgba($theme-base-2, 0.5) 12.52%, rgba($theme-base-3, 0.5) 52.01%, rgba($theme-base-4, 0.1) 100%);
